import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home'

Vue.use(Router)

export default new Router({
	mode: 'history',
 	base: window.location.pathName,
  	routes: [
	    {
	      	path: '/',
	      	name: 'Home',
	      	component: Home
	    }
  	]
})



