<style scoped="scoped">
	#ai-canvas {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 1;
	}
</style>

<template>
	<canvas ref="canvas" id="ai-canvas"></canvas>
</template>

<script>
	import {TweenMax, TweenLite} from 'gsap/TweenMax'
	import _ from 'lodash'
	import {midPointBtw, angleBtwPoints, distBtwPoints, lerp} from '@/assets/js/math'

	export default {
		name: 'AiCanvas',
		methods:{
			drawBox(){
				// LOADING
				var dialogImg = new Image();
	            dialogImg.src = require('@/assets/images/dialog-box.png');
	            dialogImg.onload = function(){
	            	init();
	            }

	            var init = (data) => {

					var points = [];
					var mouse = {x: 0, y: 0};
					var lastClicked = {
						x: 0,
						y: 0,
					}

					var updateCoords = function(event){
		                mouse.x = event.clientX;
		                mouse.y = event.clientY;

		                var point = {
		                    x: mouse.x,
		                    y: mouse.y
		                }
		                points.push(point);

		                if (points.length > 10){
		                    points.shift();
		                }
		            }

		            window.addEventListener('mousemove', updateCoords, true);
		           
					var canvas 	= this.$refs.canvas;
					var ctx 	= canvas.getContext('2d');

					var canvasSize = function(){
						canvas.width 	= window.innerWidth - 30; //Scrollbar's width
						canvas.height 	= window.innerHeight;
					}
					canvasSize();

					var elapsedTime = 0;

					window.addEventListener('resize', canvasSize , true);

			        const rect = {
			        	w: 250,
			        	h: 300,
			        	baseX: 0,
			        	baseY: 0,
			        	innerRect: {
			        		offset: 25
			        	}
			        }

			        rect.baseX = canvas.width * 0.8 - rect.w;
			        rect.baseY = (canvas.height - rect.h) / 2;

			        const dialogTexts = [
			        	"You'll never see it coming.", 
			        	"You love clicking around right?", 
			        	"Have a look at my works?",
			        	"Do you recognize this artstyle?",
			        	"It comes from Persona 5.",
			        	"It's Game of the Year for 2017."
			        ];

			        Object.freeze(dialogTexts);

			        const dialog = {
			        	posX: rect.baseX - dialogImg.width - 50,
			        	posY: rect.baseY - dialogImg.height,
			        	scl: 0,
			        	alpha: 0,
			        	textIdx: -1,
			        	lastClickedTime: 0,
			        	configs: {
			        		boxDuration: 0.5
			        	}
			        }

			        const text = {
			        	x: 0,
			        	y: 0,
			        	w: 100,
			        	h: 100,
			        	medianX: (2 * (rect.baseX) + rect.w) / 2,
			        	medianY: (2 * (rect.baseY) + rect.h) / 2,
			        }

			        text.x = rect.baseX + (rect.w - text.w) / 2;
			        text.y = rect.baseY + (rect.h - text.h) / 2;

			        const eyes = [{
			        	w: 25,
			        	h: 10,

			        	x: text.x + 5,
			        	y: text.y + 30,

			        	pupil: {
			        		size: 4,
			        		originX: 0,
			        		originY: 0,
			        		x: 0,
			        		y: 0,
			        	},

			        	offsetFromText: {
			        		x: 15,
			        		y: 30
			        	}
			        	}, {
			        	w: 25,
			        	h: 10,

			        	x: text.x + text.w - 30,
			        	y: text.y + 30,

			        	pupil: {
			        		size: 4,
			        		originX: 0,
			        		originY: 0,
			        		x: 0,
			        		y: 0,
			        	},

			        	offsetFromText: {
			        		x: 15,
			        		y: 30
			        	}
			        }];

			        for (let i = 0; i < eyes.length; i++){
			        	let eye = eyes[i];
			        	eye.pupil.originX = eye.pupil.x  = (2 * eye.x + eye.w) / 2;
			        	eye.pupil.originY = eye.pupil.y  = (2 * eye.y + eye.h) / 2 - eye.pupil.size - 1;
			        }

			        const smile = {
			        	x: text.x + 20,
			        	y: text.y + text.h + 20,
			        	h: 25,
			        };

			        const smileCPoint = {
			        	originX: (2 * smile.x + text.w) / 2,
					    originY: smile.y + smile.h,
					    x: (2 * smile.x + text.w) / 2,
					    y: smile.y + smile.h,
			        }

			        function isInActiveZone(){
			        	return (distBtwPoints(mouse, {x: text.medianX, y: text.medianY}) < canvas.width / 3.5);
			        }

			        function isClickedInDialogZone(){

			        	if (distBtwPoints(
			        			{x: lastClicked.x, y: lastClicked.y}, 
			        			{x: text.medianX, y: text.medianY}
			        		) < canvas.width / 3.5
			        	){
			        		return true
			        	};
			        	return false
			        }

			        function handleAIClick(){
				        	// Add click canvas handler
						canvas.addEventListener('click', e => {
							lastClicked.x = mouse.x;
							lastClicked.y = mouse.y;

							if (isClickedInDialogZone()){
							  	TweenLite.to(dialog, dialog.configs.boxDuration ,{
								   	scl: 1,
									alpha: 1,
								 	ease: Power1.easeOut,
							  	});

							  	// Change text
								if (dialog.textIdx < dialogTexts.length - 1){
									dialog.textIdx++;
								}
								else dialog.textIdx = 0;
							}
							else {
								TweenLite.to(dialog, dialog.configs.boxDuration ,{
								   	scl: 0,
								 	alpha: 0,
								 	ease: Power1.easeOut,
							  	});
							}

						})
			        }

			        handleAIClick();

			        function drawDialog(){

			        	ctx.save();
			        	ctx.font = "18px Arial";
			        	ctx.fillStyle = "#fafafa";

			        	ctx.translate(dialog.posX + dialogImg.width, dialog.posY + dialogImg.height);
			        	ctx.globalAlpha = dialog.alpha;
			        	ctx.rotate(10 * Math.PI / 180);

			        	ctx.drawImage(dialogImg, - dialogImg.width, 0, dialogImg.width, dialogImg.height);

			        	ctx.fillText(dialogTexts[dialog.textIdx], - dialogImg.width + 95, dialogImg.height / 2 + 18);

			        	ctx.restore();

			        }

			        function draw(){
			        	// Clear BG
						ctx.clearRect(0,0,canvas.width, canvas.height);
						ctx.fillStyle = "#111";

						if (dialog.scl > 0){
			        		drawDialog();
			        	}

			        	// Draw frame
			        	ctx.lineWidth 	= 25;

			        	var frameGrd 	= ctx.createLinearGradient(0, 0, canvas.width, 0);
			        	frameGrd.addColorStop(0, '#62deec');   
	      				frameGrd.addColorStop(1, '#7272fe');

				        ctx.fillStyle   = frameGrd;

				        ctx.fillRect(rect.baseX, rect.baseY, rect.w, rect.h );

				        ctx.fillStyle = "#111";
				        ctx.fillRect(rect.baseX + rect.innerRect.offset, rect.baseY + rect.innerRect.offset, rect.w - rect.innerRect.offset * 2, rect.h - rect.innerRect.offset * 2 );

				        ctx.fillStyle   = "#fff";
				        ctx.font 		= "200px Arial";

				        var textAngle = angleBtwPoints(
				        	{
				        		x: text.medianX,
				        		y: text.medianY
				        	},
				        	mouse
				        );

				        ctx.strokeStyle = "#fff";

				        // Draw Letter
				        ctx.save();
				        
				        if (isInActiveZone()) {
				        	ctx.transform(1,0,0,1, Math.cos(textAngle) * 0.5, 0);
				        }

				       	ctx.beginPath();

				       	ctx.lineWidth = 20;

				       	ctx.moveTo(text.x, text.y);
				       	ctx.lineTo(text.x + text.w, text.y);

				       	ctx.moveTo(text.x + text.w / 2, text.y);
				       	ctx.lineTo(text.x + text.w / 2, text.y + text.h);

				       	ctx.stroke();
				       	ctx.closePath();

				       	ctx.restore();

				       	// Draw eye

				       	ctx.strokeStyle = "#eee";
				        ctx.lineWidth 	= 2;

				        for (var i = 0; i < eyes.length; i++) {
				        	var eye = eyes[i];
				        	var pupil = eye.pupil;
				        
					       	ctx.beginPath();
					       	ctx.moveTo(eye.x, eye.y);

					       	// Below half of eye
					       	ctx.quadraticCurveTo( 
					       		(2 * eye.x + eye.w) / 2, // Median point of eye
					       		eye.y + eye.h,
					       		eye.x + eye.w,
					       		eye.y
					       	);

					       	ctx.quadraticCurveTo( 
					       		(2 * eye.x + eye.w) / 2,
					       		eye.y - eye.h,
					       		eye.x,
					       		eye.y
					       	);

					       	ctx.stroke();
						    
						    // Draw Pupil
			
					       	ctx.fillStyle = "#fafafa";
							ctx.beginPath();
							if (isInActiveZone()) {
								pupil.x = lerp(pupil.x, pupil.originX + Math.cos(textAngle) * 5, 0.1);
								pupil.y = lerp(pupil.y, pupil.originY + Math.sin(textAngle) * 1, 0.1);
					        }
					        else {
					        	pupil.x = lerp(pupil.x, pupil.originX, 0.1);
								pupil.y = lerp(pupil.y, pupil.originY, 0.1);
					        }
					        ctx.arc(
					        	pupil.x,
					        	pupil.y,
					        	pupil.size,
					        	0,
					        	2 * Math.PI
					        );

					        ctx.fill();

					        ctx.closePath();

					    }

					    // Draw smile
					    
					    ctx.beginPath();
					    ctx.strokeStyle = "fafafa";

					    ctx.moveTo(smile.x, smile.y);
					    ctx.quadraticCurveTo(
					    	smileCPoint.x - 20,
					    	smileCPoint.y,
					    	smile.x + text.w - 40,
					    	smile.y
					    );
					    ctx.fill();

					  	ctx.closePath();
			        }

			        TweenMax.ticker.addEventListener("tick", draw);
				}
			},
			pauseCanvas(){
				this.paused = true;
			},
			resumeCanvas(){
				this.paused = false;
			}
		},
		data(){
			return {
				paused: false,
			}
		},
		mounted(){
			this.drawBox();
			this.$parent.$on('pauseCanvas', this.pauseCanvas);
			this.$parent.$on('resumeCanvas', this.resumeCanvas);
		},
	}
</script>


