<style lang="scss" scoped="scoped">
	@keyframes loadingLetter {
		0% {
			opacity: 0;
			transform: translateY(0);
		}
		25% {
			transform: translateY(-25px);
			opacity: 1;
		}
		50%, 55% {
			opacity: 1;
			transform: translateY(0);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes trail1 {
		0% {
			transform: none;
		}
		40%,100% {
			transform: translate(-100px, -15px) scale(0.7);
			opacity: 0;
		}
	}

	@keyframes trail1 {
		0% {
			transform: none;
		}
		40%,100% {
			transform: translate(-100px, -15px) scale(0.7);
			opacity: 0;
		}
	}

	@keyframes trail2 {
		0% {
			transform: none;
		}
		40%,100% {
			transform: translate(-100px, -5px) scale(0.7);
			opacity: 0;
		}
	}

	@keyframes trail3 {
		0% {
			transform: none;
		}
		40%,100% {
			transform: translate(-100px, -35px) scale(0.8);
			opacity: 0;
		}
	}

	@keyframes trail4 {
		0% {
			transform: none;
		}
		40%,100% {
			transform: translate(-100px, -22px) scale(0.9);
			opacity: 0;
		}
	}
	@keyframes trail5 {
		0% {
			transform: none;
		}
		40%,100% {
			transform: translate(-100px, -32px) scale(0.7);
			opacity: 0;
		}
	}
	@keyframes trail6 {
		0% {
			transform: none;
		}
		40%,100% {
			transform: translate(-100px, -30px) scale(0.8);
			opacity: 0;
		}
	}
	@keyframes trail7 {
		0% {
			transform: none;
		}
		40%,100% {
			transform: translate(-100px, -8px) scale(0.9);
			opacity: 0;
		}
	}
	@keyframes trail8 {
		0% {
			transform: none;
		}
		40%,100% {
			transform: translate(-100px, -10px) scale(0.7);
			opacity: 0;
		}
	}


	@keyframes cube {
		0% {
			transform: none;
		}
		15% {
			transform: translateY(-50px) rotate(45deg);
		}
		30%,100% {
			transform: translateY(0) rotate(90deg);
		}
	}

	@keyframes obstacle {
		0% {
			transform: none;
		}
		100% {
			transform: translateX(-100%);
		}
	}

	.preloader {
		display: table;
		width: 100%;
		height: 100%;
		position: fixed;
		left: 0;
		right: 0;
		margin: auto;
		z-index: 99999;
		background: #111;
		&__wrap {
			position: relative;
			height: 120px;
			width: 100%;
			max-width: 450px;
			margin: auto;
		}
	}

	.loading {
		position: absolute;
		bottom: 100%;
		margin-bottom: 30px;
		width: 100%;
		text-align: center;
		text-transform: uppercase;
		font-size: 2.2em;
		color: #fafafa;
		letter-spacing: -2px;
		&__letter {
			display: inline-block;
			opacity: 0;
			animation-name: loadingLetter;
			animation-duration: 1.7s;
			animation-iteration-count: infinite;
			animation-timing-function: cubic-bezier(.67,.94,.79,1);
			@for $i from 0 to 7 {
			  	&--#{$i + 1} { 
			  		animation-delay: 0.05s * $i;
			  	}
			}
		}

		@media (min-width: 992px){
			font-size: 3em;
		}
	}

	.cube__wrap {
		overflow: hidden;
		width: 100%;
		height: 100%;
		position: relative;
	}
	.cell {
		display: table-cell;
	}
	.cell--middle {
		vertical-align: middle;
	}
	.cube, .cube--shadow {
		position: absolute;
		bottom: 4px;
		left: 50px;
		width: 40px;
		height: 40px;
		background-color: #fff;
		animation-name: cube;
		animation-delay: 1.1s;
		animation-duration: 1.7s;
		animation-iteration-count: infinite;
		-webkit-animation-timing-function: linear;
		-o-animation-timing-function: linear;
		animation-timing-function: linear;
	}

	.cube--shadow {
		opacity: 0.4;
	}

	.cube--shadow-1 {
		left: 30px;
		width: 35px;
		height: 35px;
		animation-delay: 1.15s;
	}

	.cube--shadow-2 {
		left: 20px;
		width: 30px;
		height: 30px;
		opacity: 0.2;
		animation-delay: 1.2s;
	}
	.cube--shadow-3 {
		left: 10px;
		width: 25px;
		height: 25px;
		opacity: 0.1;
		animation-delay: 1.25s;
	}


	.obstacle {
		width: 100%;
		height: 15px;
		position: absolute;
		border-right: 15px solid #eee;
		bottom: 4px;
		animation: obstacle 1.7s infinite;
		-webkit-animation-timing-function: linear;
		-o-animation-timing-function: linear;
		animation-timing-function: linear;
	}
	.road {
		height: 4px;
		width: 100%;
		left: 100%;
		position: absolute;
		bottom: 0;
		left: 0;
		background: #666;
	}

	.trail {
		width: 8px;
		height: 8px;
		position: absolute;
		left: 42px;
		bottom: 4px;
		animation-timing-function: linear;
		animation-duration: 1.7s;
		animation-iteration-count: infinite;
	}

	.trail-1 {
		background: #7344dc;
		animation-name: trail1;
		z-index: 1;

	}
	.trail-2 {
		background: #ef5200;
		animation-name: trail2;
		animation-delay: 0.1s;
		z-index: 2;
	}
	.trail-3 {
		background: #4940b3;
		animation-name: trail3;
		animation-delay: 0.2s;
		z-index: 3;
	}
	.trail-4 {
		background: #750f15;
		animation-name: trail4;
		animation-delay: 0.3s;
		z-index: 4;
	}
	.trail-5 {
		background: #6e47e7;
		animation-name: trail5;
		animation-delay: 0.4s;
		z-index: 5;
	}
	.trail-6 {
		background: #b96320;
		animation-name: trail6;
		animation-delay: 0.5s;
		z-index: 6;
	}
	.trail-7 {
		background: #28a745;
		animation-name: trail7;
		animation-delay: 0.6s;
		z-index: 7;
	}
	.trail-8 {
		background: #ef5200;
		animation-name: trail8;
		animation-delay: 0.7s;
		z-index: 8;
	}
	.trail-9 {
		background: #09c5c1;
		animation-name: trail1;
		animation-delay: 0.8s;
		z-index: 9;
	}
	.trail-10 {
		background: #4940b3;
		animation-name: trail2;
		animation-delay: 0.9s;
		z-index: 10;
	}
	.trail-11 {
		background: #7344dc;
		animation-name: trail3;
		animation-delay: 1s;
		z-index: 11;
	}

	.fade-enter-active, .fade-leave-active {
	  	transition: opacity .5s;
	}
	.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
	 	 opacity: 0;
	}


</style>

<template>
	<transition name="fade">
		<div class="preloader" v-show="!isWindowLoaded">
		<div class="cell cell--middle">
			<div class="preloader__wrap">
				<div class="cube__wrap">
					<div class="cube cube--shadow cube--shadow-1"></div>
					<div class="cube cube--shadow cube--shadow-2"></div>
					<div class="cube cube--shadow cube--shadow-3"></div>
					<div class="cube"></div>
					<div class="road"></div>
					<div class="obstacle"></div>
				</div>

				<div class="loading">
					<span class="loading__letter loading__letter--1">L</span>
					<span class="loading__letter loading__letter--2">o</span>
					<span class="loading__letter loading__letter--3">a</span>
					<span class="loading__letter loading__letter--4">d</span>
					<span class="loading__letter loading__letter--5">i</span>
					<span class="loading__letter loading__letter--6">n</span>
					<span class="loading__letter loading__letter--7">g</span>
				</div>

				<div class="trail trail-1"></div>
				<div class="trail trail-2"></div>
				<div class="trail trail-3"></div>
				<div class="trail trail-4"></div>
				<div class="trail trail-5"></div>
				<div class="trail trail-6"></div>
				<div class="trail trail-7"></div>
				<div class="trail trail-8"></div>
				<div class="trail trail-9"></div>
				<div class="trail trail-10"></div>
				<div class="trail trail-11"></div>
			</div>
		</div>
		</div>
	</transition>
</template>

<script>
	export default {
		name: 'Preloader',
		data(){
			return {
                isWindowLoaded: false,
                isTimerFinished: false,
                minLoadingTimer: null // Show preloading at least 2s
            }
		},
		props: {
			loadingTimeout: {
				type: Number,
				default: 2000
			},
		},
		methods:{
			loaded(){
				this.isWindowLoaded = true;
				this.$emit('loaded');
			},
			handleWindowLoad(){
				if (this.isTimerFinished){
					this.loaded();
				}
            }
		},
		mounted(){
			this.minLoadingTimer = setTimeout(() => {
				if (!this.isWindowLoaded){
					this.loaded();
				}
				clearTimeout(this.minLoadingTimer);
			}, this.loadingTimeout);

			window.addEventListener('load', this.handleWindowLoad);
		},
		beforeDestroy(){
            window.removeEventListener('load', this.handleWindowLoad);
        },
	}
</script>


