<template>
    <section class="section">
        <div ref="logo" class="section__logo">
            <router-link to=""><img src="@/assets/images/logo.png" width="90" alt="Terry Do - Frontend Website Developer"></router-link>
        </div>

        <div id="canvas-wrap" ref="bgcanvas">
            <wave-canvas></wave-canvas>
            <ai-canvas v-not-visible="'tablet_landscape'"></ai-canvas>
            <div @click="$emit('navPls', 2)">
                <section-nav-line title="Featured Works"></section-nav-line>
            </div>
        </div>
        
        <div ref="sectionDOM" class="section__inner text-left">
            <div class="intro container">
                <div ref="introDOM" class="d-ib">
                    <span class="intro__line intro__line--1">
                        <letters ref="line1" :letters="line1Letters"></letters>
                    </span>
                    <h1 class="intro__line intro__line--2">
                        <letters ref="line2" :letters="line2Letters"></letters>
                    </h1>
                    <h2 class="intro__line intro__line--3">
                        <letters ref="line3" :letters="line3Letters"></letters>
                    </h2>
                </div>
            </div>
        </div>
   
    </section>
</template>

<script>

    import WaveCanvas       from '@/components/wave-canvas'
    import AiCanvas         from '@/components/ai-canvas'
    import Letters          from '@/components/letters'
    import SectionNavLine   from '@/components/section-nav-line'

    import {TweenMax, TimelineMax}      from 'gsap/TweenMax'
    import mixins                       from '@/mixins/section-animate.js'
    import TweenList                    from '@/assets/js/tweenList.js'
    import {splitText}                  from '@/assets/js/utils'
    
    export default {
        name: 'SectionIntro',
        components: {
            WaveCanvas,
            AiCanvas,
            Letters,
            SectionNavLine
        },
        mixins: [mixins],
        data(){
            return {
                line1Text: "Hi, I'm",
                line2Text: "Terry Do",
                line3Text: "Full-stack Web Developer",
            }
        },
        computed: {
            perspectiveOrigin(){
                var introDomClientRect = this.$refs.introDOM.getBoundingClientRect();
                var coords = {
                    x: introDomClientRect.left,
                    y: (introDomClientRect.bottom + introDomClientRect.top) / 2
                }
                return coords;
            },
            line1Letters(){
                return splitText(this.line1Text);
            },
            line2Letters(){
                return splitText(this.line2Text);
            },
            line3Letters(){
                return splitText(this.line3Text);
            },
        },
        methods:{
            animateIn(){
                this.$emit('resumeCanvas');

                let line1LettersDOM = this.$refs.line1.$refs.letters;
                let line2LettersDOM = this.$refs.line2.$refs.letters;
                let line3LettersDOM = this.$refs.line3.$refs.letters;

                let tl = new TimelineMax({
                    onComplete: () => {
                        this.emitSectionIn();
                    }
                });

                tl.addLabel("start");

                tl.to(this.$refs.sectionDOM, 1, TweenList.fadeInRight.to, 'start');
                tl.to(this.$refs.bgcanvas, 1, TweenList.fadeIn.to,  'start');
                tl.to(this.$refs.logo, 1, TweenList.fadeIn.to,  'start');

                tl.staggerFromTo(line1LettersDOM, 0.5, TweenList.fadeInUp.from, TweenList.fadeInUp.to, 0.05)

                tl.staggerFromTo(line2LettersDOM, 1, TweenList.fadeIn.from, TweenList.fadeIn.to, 0.05, '-=0.3')

                tl.staggerFromTo(line3LettersDOM, 0.5, TweenList.fadeInUp.from, TweenList.fadeInUp.to, 0.02, '-=1') 
            },
            animateOut(){
                let tl = new TimelineMax({
                    onComplete: () => {
                        this.emitSectionOut();
                        this.$emit('pauseCanvas');
                    }
                });
                tl.addLabel('startLabel');

                tl.to(this.$refs.sectionDOM, 1, TweenList.fadeOutLeft.to, 'startLabel');
                tl.to(this.$refs.logo, 1, TweenList.fadeOut.to, 'startLabel');
                tl.to(this.$refs.bgcanvas, 1, TweenList.fadeOut.to, 'startLabel');
            },
        },
    }
</script>


