<style lang="scss">
	$thickness: 2px;
	$bg: #444;
	$textColor: #bbb;

	.navline {
		z-index: 8;
		position: relative; 
		cursor: pointer;
		position: fixed;
		bottom: 7%;
		left: 0;
		right: 0;
		margin: auto;
		line-height: 1;
		text-align: center;
		&__line {
			position: relative;
			display: inline-block;			
			background-color: #444;
			height: $thickness;
			width: 80px;
			overflow:hidden;
			z-index: 5;
			&:before {
				content: '';
				position: absolute;
				top: 0;
				right: 0;
				width: 40%;
				height: $thickness;
				background: $textColor;
				animation-name: sectionNavLineMobile;
				animation-timing-function: linear;
				animation-duration: 2s;
				animation-iteration-count: infinite;
			}
		}
		&__title {
			position: relative;
			display: block;
			font-size: 1em;
			color: $textColor;
			width: 100%;
			text-align: center;
		}

		&__mobile-note {
			display: block;
			color: $textColor;
			margin-top: 10px;
			animation-name: breath;
			animation-duration: 3s;
			animation-iteration-count: infinite;
		}

		@media (min-width: 992px){
			&__title {
				position: fixed;
				bottom: 8.5%;
			}
			&__line {
				position: fixed;
				bottom: 0;
				height: 7.5%;
				width: $thickness;
				&:before {
					top: 0;
					left: 0;
					right: auto;
					width: $thickness;
					height: 40%;
					animation-name: sectionNavLine;
				}
			}
			&__mobile-note {
				display: none;
				animation: none;
			}
		}
	}

	@keyframes breath {
		0%, 70%, 100% {
			opacity: 0.5;
		}
		35% {
			opacity: 1;
		}
	}

	@keyframes sectionNavLineMobile {
		0% {
			right: -40%;
		}
		60%, 100% {
			right: 100%;
		}
	}

	@keyframes sectionNavLine {
		0% {
			top: -40%;
		}
		60%, 100% {
			top: 100%;
		}
	}
</style>

<template>
	<div class="navline">
		<span class="navline__title">{{title}}</span>
		<div class="navline__line"></div>
		<small class="navline__mobile-note">Swipe left</small>
	</div>
	
</template>

<script>
	export default {
		props: {
			title: {
				type: String,
				default: 'Next Section'
			}
		},
	}
</script>


