<template>
	<main ref="main" @scroll.passive="detectScroll">
	    <preloader @loaded="handleLoaded"></preloader>

        <td-menu :current-section="currentSection" @navPls="jumpSection"></td-menu>

	    <section-intro 
	        :class="{'section--show': currentSection == 1}" 
	        :section-id="1" 
	        class="page"
	        @sectionOut="onSectionOut"
	        @sectionIn="onSectionIn"
            @navPls="jumpSection"
	    >
	    </section-intro>
	    <section-project 
	        :class="{'section--show': currentSection == projectIndex + 2}" 
	        :section-id="projectIndex + 2" 
	        class="page"
	        @sectionOut="onSectionOut"
	        @sectionIn="onSectionIn"
	        v-for="(project, projectIndex) in projectData"
	        v-bind="project"
	        :key="projectIndex"
	    ></section-project>
        <section-about
            :class="{'section--show': currentSection == aboutSectionIndex}"
            :section-id="aboutSectionIndex" 
            class="page"
            @sectionOut="onSectionOut"
            @sectionIn="onSectionIn"
        ></section-about>
	</main>
	
</template>

<script>
    import Preloader                        from '@/components/preloader'
    import TdMenu                           from '@/components/menu'
    import SectionIntro                     from '@/components/section-intro'
    import SectionProject                   from '@/components/section-project'
    import SectionAbout                     from '@/components/section-about'
    import {TweenMax, Power2, TimelineLite} from 'gsap/TweenMax'

    export default {
        name: 'Home',
        components: {
            SectionIntro,
            SectionProject,
            SectionAbout,
            Preloader,
            TdMenu
        },
        data(){
            return {
                currentSection: 1,
                isAnimating: true,
                targetSection: null,
                projectData: [
                    {
                        projectName: "Boozez",
                        projectDescription: "No.1 Drinking Portal in Singapore. My client requested me to customize theme, improve search feature and fix some bugs in a short period.",
                        projectLink: "https://boozez.com",
                        projectBackground: "/static/images/boozez/background.jpg",
                        projectImage: "/static/images/boozez/desktop.jpg",
                        projectImageMobile: "/static/images/boozez/mobile.jpg",
                    },
                    {
                        projectName: "Tet Nha Ngoai",
                        projectDescription: "A contest page for Tet Holiday. This site's built with Wordpress and tracked users' votes with Facebook Graph API. I took part in both frontend and backend development.",
                        projectLink: "https://tetnhangoai.com",
                        projectBackground: "/static/images/tetnhangoai/background.jpg",
                        projectImage: "/static/images/tetnhangoai/desktop.jpg",
                        projectImageMobile: "/static/images/tetnhangoai/mobile.jpg",
                    },
                    {
                        projectName: "iPOS",
                        projectDescription: "A Vietnamese and Singapore Joint Stock Company offering Food and Beverage Solutions: POS Equipments, Consultants. I did the PSD to HTML work and collaborate developing Backend with my colleague.",
                        projectLink: "http://ipos.com",
                        projectBackground: "/static/images/ipos/background.jpg",
                        projectImage: "/static/images/ipos/desktop.jpg",
                        projectImageMobile: "/static/images/ipos/mobile.jpg",
                    },
                    {
                        projectName: "Deception: MDHK",
                        projectDescription: "My online boardgame pet project based in Deception: Murder in Hong Kong made to play with my friends in leisure time, built with Swoole - a production-grade async programming framework for PHP, and Vue.js",
                        projectLink: "https://deceptionbg.000webhostapp.com",
                        projectBackground: "/static/images/deception/background.jpg",
                        projectImage: "/static/images/deception/desktop.jpg",
                        isProjectImageFullScreen: true,
                    },
                ]
            }
        },
        computed: {
            totalSection(){
                return this.projectData.length + 2
            },
            aboutSectionIndex(){
                return 1 + this.projectData.length + 1
            }
        },
        methods: {
            animateSection(id, type){
                this.isAnimating = true;
                this.$emit('animatePlz', {id, type});
            },
            hideCurrentSection(){
                this.animateSection(this.currentSection, 'out');
            },
            jumpSection(targetId){
                if (this.isAnimating === true) return;
                
                this.targetSection = targetId;
                this.hideCurrentSection();
            },
            nextSection(){
                if (this.currentSection === this.totalSection || this.isAnimating === true){
                    return;
                }

                this.targetSection = this.currentSection + 1;
                this.hideCurrentSection();
            },
            previousSection(){
                if (this.currentSection === 1 || this.isAnimating === true) {
                    return;
                }

                this.targetSection = this.currentSection - 1;
                this.hideCurrentSection();
            },
            onSectionOut(){
                this.currentSection = this.targetSection;
                this.animateSection(this.targetSection, 'in');
            },
            onSectionIn(){
                this.isAnimating = false;
            },
            detectScroll(event){
                let delta;

                if (event.wheelDelta){
                    delta = event.wheelDelta;
                }else{
                    delta = -1 * event.deltaY;
                }

                if (delta < 0){
                    return 'down';
                }  if (delta > 0){
                    return 'up';
                }
            },
            handleKeyboardEvent(e){
                if (e.key === 'ArrowDown' || e.key === 'PageDown') {
                    this.nextSection();
                }
                if (e.key === 'ArrowUp' || e.key === 'PageUp') {
                    this.previousSection();
                }
            },
            handleScrollEvent(e){
                if (this.detectScroll(e) === 'down'){
                    this.nextSection();
                }
                else this.previousSection();
            },
            handleTouchEvent(){
                var xDown = null;                                                        
                var yDown = null;

                const handleTouchStart = (evt) => {                                         
                    xDown = evt.touches[0].clientX;                                      
                    yDown = evt.touches[0].clientY;                                      
                }                                            

                const handleTouchMove = (evt) => {
                    if ( ! xDown || ! yDown ) {
                        return;
                    }

                    let xUp = evt.touches[0].clientX;                                    
                    let yUp = evt.touches[0].clientY;

                    let xDiff = xDown - xUp;
                    let yDiff = yDown - yUp;

                    if ( Math.abs( xDiff ) > Math.abs( yDiff ) ) {/*most significant*/
                        if ( xDiff > 0 ) {
                            this.nextSection();
                        } else {
                            this.previousSection();
                        }                       
                    }

                    /* reset values */
                    xDown = null;
                    yDown = null;                                             
                }

                document.addEventListener('touchstart', handleTouchStart, false);        
                document.addEventListener('touchmove', handleTouchMove, false);                                               
            },
            handleLoaded(){
                this.animateSection(1, 'in');
            }
        },
        mounted(){
            this.handleTouchEvent();
            window.addEventListener('keydown', this.handleKeyboardEvent);
            window.addEventListener('wheel', this.handleScrollEvent);
        },
        beforeDestroy(){
            window.removeEventListener('keydown', this.handleKeyboardEvent);
        },
    }
</script>


