<template>
	<section ref="section" class="section">
		<div class="section__inner">
			<div class="project container">
				<div ref="projectBg" class="project__bgwrap">
					<img ref="canvasBg" class="project__background" :src="projectBackground" alt="BG">
					<canvas ref="canvas" v-not-visible="'tablet_landscape'" style="z-index: 1;position: fixed;top: 0;left: 0;"></canvas>
				</div>


				<div class="row gutter-50">
					<div class="col-12 col-lg-6 project__info">
						<h2 class="project__name">
							<letters ref="projectName" :letters="nameLetters"></letters>
						</h2>
						<div ref="projectDescription" class="project__description">{{projectDescription}}</div>
						<a :href="projectLink" target="_blank" class="btn btn--link project__btn hidden" ref="visitBtn">Visit Site</a>
					</div>

					<div class="col-12 col-lg-6 project__image">
						<div class="macbook" ref="macbook">
							<div class="macbook__screen macbook__screen--covered">
								<img class="macbook__frame" ref="projectFrame" src="@/assets/images/macbook-frame.png" alt="MacBook Frame">
								<img class="macbook__image" :class="{'macbook__image--full' : isProjectImageFullScreen}" ref="projectImage" :src="projectImage" alt="MacBook Image">
							</div>

							<div class="macbook__keyboard">
								<img src="@/assets/images/macbook-keyboard.png" alt="MacBook Keyboard" class="macbook__keyboard">
							</div>					
							
						</div>

						<div v-show="projectImageMobile" class="iphone" ref="iphone">
							<img src="@/assets/images/iphone.png" alt="iphone" class="iphone__frame">
							<img :src="projectImageMobile" class="iphone__image" alt="iPhone's project image">
						</div>
					</div>
				</div>
			</div>
		</div>
	</section>
</template>

<script>
	import {TweenMax, SteppedEase} 		from 'gsap/TweenMax'
	import TweenList   		from '@/assets/js/tweenList'
	import {splitText} 		from '@/assets/js/utils'
	import {round, lerp} 	from '@/assets/js/math'
	import mixins 			from '@/mixins/section-animate.js'
	import Letters  		from '@/components/letters'

	export default {
		name: 'SectionProject',
		props: ['sectionId'],
		data(){
			return {
				scrollTl: null,
				canvasPaused: true, // Canvas state
			}
		},
		computed: {
			nameLetters(){
				return splitText(this.projectName);
			}
		},
		props: {
			projectName: {
				type: String,
				required: true
			},
			projectDescription: {
				type: String,
				required: true
			},
			projectLink: {
				type: String,
				default: '#',
			},
			projectImage: {
				type: String,
				required: true
			},
			projectBackground: {
				type: String,
				required: true
			},
			projectImageMobile: {
				type: String,
			},
			isProjectImageFullScreen: {
				type: Boolean,
			},
		},
		components:{
			Letters,
		},
		mixins:[mixins],
		methods: {
			animateIn(){
				let nameLetters = this.$refs.projectName.$refs.letters;
				this.canvasPaused 	= false;

				let tl = new TimelineMax({
                    onComplete: () => {
                        this.emitSectionIn();

                        if (!this.isProjectImageFullScreen){
                        	scrollProjectImage(this);
                        }
                        
                    }, 
				});

				// Macbook
				tl.addLabel('startLabel');

				tl.staggerFromTo(nameLetters, 0.5, TweenList.fadeInRight.from, TweenList.fadeInRight.to, 0.05, 'startLabel');	
				tl.fromTo(this.$refs.canvasBg, 1, TweenList.fadeIn.from, TweenList.fadeIn.to, 'startLabel');

				tl.addLabel('phase2Label');
				tl.fromTo(this.$refs.projectDescription, 0.5, TweenList.fadeInUp.from, TweenList.fadeInUp.to, 'startLabel');
				tl.fromTo(this.$refs.macbook, 0.5, TweenList.fadeInRight.from, TweenList.fadeInRight.to, 'startLabel');
				tl.fromTo(this.$refs.iphone, 0.5, TweenList.fadeInLeft.from, TweenList.fadeInLeft.to, 'startLabel');
				tl.fromTo(this.$refs.visitBtn, 0.5, TweenList.fadeIn.from, TweenList.fadeIn.to, '-=0.6')

				function scrollProjectImage(self) {
					if (self.scrollTl === null){
						self.scrollTl = new TimelineMax({
							repeat: -1,
							yoyo: true,
							repeatDelay: 2,
							paused: true,
						});
					}
					else {
						self.scrollTl.restart();
						self.scrollTl.pause();
					}

					let projectFrame = self.$refs.projectFrame;
					let projectImage = self.$refs.projectImage;

					let y = projectImage.getBoundingClientRect().height - projectFrame.getBoundingClientRect().height;

					self.scrollTl.to(projectImage, 10, {
						y: -y,
						ease: Power0.easeNone,
					})

					self.$refs.macbook.addEventListener('mouseover', event => {
						self.scrollTl.play();
					})

					self.$refs.macbook.addEventListener('mouseleave', event => {
						self.scrollTl.pause();
					})
				}						
			},
			animateOut(){
				let nameLetters = this.$refs.projectName.$refs.letters;

				let tl = new TimelineMax({
                    onComplete: () => {
                        this.emitSectionOut();
                        this.canvasPaused = true;

                        if (!this.isProjectImageFullScreen){
	                        this.scrollTl.restart();
	                        this.scrollTl.pause();
	                    }
                    }
				});

				// Macbook
				tl.addLabel('startLabel');

				tl.staggerFromTo(nameLetters, 0.5, TweenList.fadeOutLeft.from, TweenList.fadeOutLeft.to, 0.05, 'startLabel');
				tl.fromTo(this.$refs.projectDescription, 0.5, TweenList.fadeOutDown.from, TweenList.fadeOutDown.to, 'startLabel');

				tl.fromTo(this.$refs.macbook, 0.5, TweenList.fadeOutLeft.from, TweenList.fadeOutLeft.to, 'startLabel');
				tl.fromTo(this.$refs.iphone, 0.5, TweenList.fadeOutRight.from, TweenList.fadeOutRight.to, 'startLabel');
				tl.fromTo(this.$refs.canvasBg, 0.5, TweenList.fadeOut.from, TweenList.fadeOut.to, 'startLabel');
				tl.fromTo(this.$refs.visitBtn, 0.5, TweenList.fadeOut.from, TweenList.fadeOut.to, 'startLabel')
			},

			canvasInit(){
				let canvas 		= this.$refs.canvas;

				let w = canvas.width 	= window.innerWidth;
				let h = canvas.height  	= window.innerHeight;

				window.addEventListener('resize', () => {
					w = canvas.width 	= window.innerWidth;
					h = canvas.height  	= window.innerHeight;
				})

				let img = this.$refs.canvasBg;
				let ctx = canvas.getContext('2d');

				let mouseX = 0, mouseY = 0;
				
				let radialGradControlPoint = 0.5;
				let elapsedTime = 1;

				var circle = {
					lastX: w / 2,
					lastY: h / 2
				}

				function drawBg(){
					ctx.fillStyle = 'rgba(0,0,0,0.82)';
                	ctx.fillRect(0, 0, w, h);
				}

				drawBg();

	            function draw(cx, cy, radius) {

	                ctx.save();
	                ctx.clearRect(0, 0, w, h);

	                drawBg();

	                if (circle.lastX === 0 && circle.lastY === 0) return;

	                var radialGradient = ctx.createRadialGradient(cx, cy, 1, cx, cy, radius);

	                radialGradient.addColorStop(0, 'rgba(0, 0, 0, 0.5)');
	                radialGradient.addColorStop(radialGradControlPoint, 'rgba(0, 0, 0, 0.3)');
	                radialGradient.addColorStop(1, 'rgba(0, 0, 0, 0)');

	                ctx.beginPath();

	                ctx.globalCompositeOperation = 'destination-out';

	                ctx.arc(cx, cy, radius, 0, Math.PI * 2, false);
	                ctx.fillStyle = radialGradient;
	                ctx.fill();

	                ctx.restore();
	            }

	            this.handleMouseMove = e => {
	                mouseX = parseInt(e.clientX);
	                mouseY = parseInt(e.clientY);
	            }

	            this.loop = () =>{
	            	if (this.canvasPaused === false) {
		            	circle.lastX = lerp(circle.lastX, mouseX, 0.05);
	  					circle.lastY = lerp(circle.lastY, mouseY, 0.05);

		            	draw(circle.lastX, circle.lastY, w / 4);
		            }
	            	requestAnimationFrame(this.loop);
	            }

	            this.loop();

	            this.$refs.section.addEventListener('mousemove', this.handleMouseMove, false);
            	this.$refs.section.addEventListener('touchmove', this.handleMouseMove, false);
			}
		},
		mounted(){
			if (this.$refs.canvas){
				this.canvasInit();
			}
		},
	}
</script>


