
<template>
     <section ref="section" class="section section--scrollable">
        
        <particle-canvas v-if="isInited"></particle-canvas>

        <div class="section__inner">
            <div class="about container">
                <div class="row gutter-50">

                    <div class="col-12 col-lg-6 stack">
                        <div class="ab-title">
                            <div class="ab-title__text">
                                <h2 class="ab-title__dummy">ABOUT ME</h2>
                                <span ref="aboutText">ABOUT ME</span>
                            </div>
                            <div ref="aboutLine" class="ab-title__line"></div>
                        </div>
                        
                        <div class="row" ref="stackLists">
                            <ul class="col-6 stack__list d-ib-mg v-top">
                                <li class="stack__item">Frontend Stacks</li>
                                <li class="stack__item">Vue.js / Nuxt.js</li>
                                <li class="stack__item">SASS</li>
                                <li class="stack__item">SVG/Canvas</li>
                                <li class="stack__item">Bootstrap 3/4</li>
                            </ul>

                            <ul class="col-6 stack__list d-ib-mg v-top">
                                <li class="stack__item">Backend Stacks</li>
                                <li class="stack__item">Wordpress</li>
                                <li class="stack__item">FuelPHP</li>
                                <li class="stack__item">CodeIgniter</li>
                                <li class="stack__item">Laravel</li>
                            </ul>

                            <div class="col-12">
                                <small class="stack__note">Those are my favorite technology stacks and I'm adroit at using them. However, I'd gladly work on other relevant stacks which were not listed above.</small>
                            </div>
                        </div>

                        
                    </div>

                    <div class="col-12 col-lg-6 ab-profile" ref="profile">
                        <div class="text-center">
                            <img class="ab-profile__avatar img-circle img-border" src="@/assets/images/avatar.jpg" alt="Terry Do's face">
                            <h3 class="ab-profile__name">
                                Terry Do<br>
                                <small>"Đỗ Bảo Trung"</small>
                            </h3>
                        </div>

                        <p class="ab-profile__desc">
                            I'm a Website Developer living in Hanoi, a school-hater yet always yearn for more knowledge and improve himself. My ambition is simple: working abroad and drinking milk coffee every day.
                        </p>

                        <ul class="contact text-center">
                            <li class="contact__item d-ib v-middle"><a href="tel:+01634757969"><img src="@/assets/images/icon-call.png" alt="Call me maybe"></a></li>
                            <li class="contact__item d-ib v-middle"><a target="_blank" href="https://facebook.com/Trug.Can"><img src="@/assets/images/icon-fb.png" alt="Inbox me maybe"></a></li>
                            <li class="contact__item d-ib v-middle"><a href="mailto:hou.dobaotrung@gmail.com"><img src="@/assets/images/icon-mail.png" alt="Contact me maybe"></a></li>
                        </ul>
                    </div>
                    
                </div>
            </div>
        </div>
   
    </section>
</template>

<script>
    import mixins from '@/mixins/section-animate.js'
    import { TweenMax, TimelineMax } from 'gsap/TweenMax'
    import TweenList from '@/assets/js/tweenList'

    import ParticleCanvas from '@/components/particle-canvas'

    export default {
        name: 'SectionAbout',
        mixins: [mixins],
        components: {
            ParticleCanvas
        },
        data(){
            return {
                isInited: false,
            }
        },
        methods: {
            animateIn(){
                TweenMax.set(this.$refs.aboutLine, {
                    scaleX: 0
                })

                this.isInited = true;

                let tl = new TimelineMax(
                    {
                        onComplete: () => {
                            this.emitSectionIn();
                        }
                    }
                );

                tl.set(this.$refs.section, {opacity: 1})

                tl.addLabel("start");

                tl.fromTo(this.$refs.aboutText, 0.75, {
                    yPercent: 100
                }, {
                    yPercent: 0
                }, "start")

                tl.to(this.$refs.aboutLine, 0.5, {
                    scaleX: 1,
                    transformOrigin: "0% 50%",
                }, "start")

                tl.to(this.$refs.aboutLine, 0.5, {
                    scaleX: 0,
                    transformOrigin: "100% 50%",
                }, "0.5")

                tl.fromTo(this.$refs.stackLists, 1, TweenList.fadeInUp.from, TweenList.fadeInUp.to, "-=0.2");
                tl.fromTo(this.$refs.profile, 1 , TweenList.fadeInLeft.from, TweenList.fadeInLeft.to, "-=0.2");
            },
            animateOut(){
                let tl = new TimelineMax({
                    onComplete: () => {
                        this.emitSectionOut();
                    }
                });
                tl.fromTo(this.$refs.section, 1, TweenList.fadeOut.from, TweenList.fadeOut.to);
                
            }
        },
    }
</script>


