<style lang="scss">
	$gap: 14px;
	$width: 35px;
	$thickness: 2px;

	.menu-toggle {
		$self: &;
		position: fixed;
		right: 10%;
		top: 7%;
		z-index: 99;
		cursor: pointer;
		width: $width;
		height: #{($thickness + $gap) * 3};
		will-change: transform;
		transition: 0.35s all;

		&__line {
			transition: 0.35s all;
			width: $width;
			height: $thickness;
			background-color: #ccc;
			display: block;
			position: absolute;
			margin-bottom: 10px;
			will-change: transform;
			@for $i from 1 through 3 {
				&:nth-child(#{$i}){
					top: ($i - 1) * $gap;
				}
			}
		}

		&:hover, &--active {
			#{$self}__line {
				background-color: #fafafa;
			}
		}

		&--active {
			transform: scale(1.1);
			#{$self}__line {
				&:nth-child(1){
					transform-origin: 0 0;
					transform: translate(3px, 1px) rotate(45deg);
				}
				&:nth-child(2){
					opacity: 0;
				}
				&:nth-child(3){
					transform-origin: 0 0;
					transform: translate(1px, -3px) rotate(-45deg);
				}
			}
		}
	}

	.menu {
		position: fixed;
		width: 100%;
		height: 100%;
		opacity: 0;
		background: rgba(0,0,0,0.9);
		color: #ccc;
		z-index: 9;
		text-align: center;
		font-size: 1.1em;
		display: table;
		z-index: 9;
		visibility: hidden;
		&--active {
			z-index: 9;
		}
		&__list {
			display: table-cell;
			vertical-align: middle;
		}
		&__item {
			margin-bottom: 15px;
		}
		&__link {
			color: #ccc;
			transition: 0.35s color;
			font-weight: 300;
			&:hover{
				color: #fafafa;
			}
		}

		@media (min-width: 768px) {
			font-size: 2em;
		}
	}
</style>

<template>
	<div>
		<div @click="toggleMenu" class="menu-toggle" :class="{'menu-toggle--active': isActive}">
			<span class="menu-toggle__line"></span>
			<span class="menu-toggle__line"></span>
			<span class="menu-toggle__line"></span>
		</div>

		<nav ref="menu" class="menu" :class="{'menu--active': isActive}">
			<ul class="menu__list">
				<li class="menu__item" v-for="item in menuItems" ref="menuItems">
					<a class="menu__link" href="#" @click="emitNav(item.sectionId,$event)">{{item.name}}</a>
				</li>
			</ul>
		</nav>
	</div>
</template>

<script>
	import {TweenMax} from 'gsap/TweenMax'
	import TweenList from '@/assets/js/tweenList'
	export default {
		name: "Menu",
		data(){
			return {
				isAnimating: false,
				isActive: false,
				menuItems: [
					{
						name: "- Home -",
						sectionId: 1,
					},
					{
						name: "Boozez",
						sectionId: 2,
					},
					{
						name: "Tet Nha Ngoai",
						sectionId: 3,
					},
					{
						name: "iPOS",
						sectionId: 4,
					},
					{
						name: "Deception: MDHK",
						sectionId: 5,
					},
					{
						name: "- About me -",
						sectionId: 6,
					}
				]
			}
		},
		props: {
			currentSection: {
				type: Number,
			}
		},
		methods: {
			toggleMenu(){
				if (this.isAnimating) return;

				this.isAnimating = true;

				if (!this.isActive){
					this.animateIn();
				}
				else this.animateOut();
			},
			animateIn(){
				this.isActive = true;

				let tl = new TimelineMax({
					ease: Power0.easeNone,
					onComplete : () => {
						this.isAnimating = false;
					}
				});

				TweenMax.set(this.$refs.menu, {
					css: {
						visibility: 'visible'
					}
				})

				tl.fromTo(this.$refs.menu, 0.3, TweenList.fadeIn.from, TweenList.fadeIn.to);
				tl.staggerFromTo(this.$refs.menuItems, 0.5, TweenList.fadeInUp.from, TweenList.fadeInUp.to, 0.075, '-=0.1');
			},
			animateOut(){
				this.isActive = false;

				let tl = new TimelineMax({
					onComplete : () => {
						this.isAnimating = false;
						TweenMax.set(this.$refs.menu, {
							css: {
								visibility: 'hidden'
							}
						})
					}
				});

				tl.fromTo(this.$refs.menu, 0.35, TweenList.fadeOut.from, TweenList.fadeOut.to);
			},
			emitNav(sectionId, e){
				e.preventDefault();

				if (sectionId == this.currentSection || this.isAnimating === true) return;

				this.animateOut();
				this.$emit('navPls', sectionId);
			}
		},
	}
</script>


