<style scoped="scoped">
	.letters, .letter {
		display: inline-block;
	}
</style>

<template>
	<span class="letters">
		<span class="letter" ref="letters" v-for="letter in letters">
			{{letter === ' ' ? '&nbsp;' : letter}}
		</span>
	</span>
</template>

<script>
	export default {
		name: 'Letters',
		props: ['letters'],
	}
</script>


