        :particleSize="3"
        :linesDistance="220"
        :moveSpeed="5"
        class="particle-canvas"
    ></vue-particles> -->

	<div id="particles-canvas"></div>

</template>

<script>
	import 'particles.js'

	export default {
		name: 'ParticleCanvas',
		mounted(){
			window.particlesJS.load('particles-canvas', '/static/js/particlesJS-config.json', ()=> {
				console.log("??");
			})
		},
		beforeDestroy(){
			// console.log("I'm destroyed");
			// window.removeEventListener('resize', this.setCanvasSize);
		}
	}
</script>


