<style scoped="scoped">
	#wave-canvas {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
</style>

<template>
	<canvas ref="canvas" id="wave-canvas"></canvas>
</template>

<script>
	import _ from 'lodash'
	import {midPointBtw, angleBtwPoints, distBtwPoints, lerp} from '@/assets/js/math'

	export default {
		name: 'WaveCanvas',
		methods:{
			drawWaves(){
				var self = this;

				if (typeof _ !== 'function'){
					console.warn("Lodash is required.");
				}

				var canvas 			= this.$refs.canvas;
				var ctx 			= canvas.getContext('2d');
				var waveContainer 	= null;
		
				function Wave(settings){
					this.defaultSetting = {
						curveCount: 4, // define number of curves
						maxYOffset: 10,
						color: "#000",
						baseY: canvas.height / 2,
						speed: 0.03,
					};
					this.settings = {...this.defaultSetting, ...settings};
					this.cPoints = [];

					this.radian = 0;

					this.generateCPoints();
				}

				Wave.prototype.generateCPoints = function(){
					var sign 		= 1;
					var curveCount 	= this.settings.curveCount;
					for (var i = curveCount; i >= 0; i--) {

						var sectionWidth = canvas.width / curveCount; 

						var x = canvas.width - sectionWidth * i;
						var y = this.settings.baseY + Math.random() * this.settings.maxYOffset * sign;
		        
						this.cPoints.push({x: x,y: y, sign: sign});
						sign = -sign;
					}
				}

				Wave.prototype.update = function(){
					this.radian += this.settings.speed;

					var rad = this.radian;

					_.forEach(this.cPoints, (cPoint) => {
						if (cPoint.sign > 0){
							cPoint.y += Math.cos(rad);
						}
						else {
							cPoint.y += Math.sin(rad);
						}
					})
				}

				Wave.prototype.draw = function(){
					this.update();

					var curveCount = this.settings.curveCount;

					ctx.save();

					ctx.strokeStyle = this.settings.color;
					ctx.shadowBlur 	= 25;
					ctx.shadowColor = this.settings.color;
					ctx.lineWidth = 1;
					ctx.lineCap   = 'round';
					ctx.lineJoin  = 'round';

					ctx.beginPath();
					ctx.moveTo(this.cPoints[0].x, this.cPoints[0].y);

					for (var key = 0; key < curveCount; key++) {
	
						var cPoint 		= this.cPoints[key];
						var nextCPoint 	= this.cPoints[key+1];

						var midPoint = midPointBtw(cPoint, nextCPoint);

						ctx.quadraticCurveTo(cPoint.x, cPoint.y, midPoint.x, midPoint.y);
						
						if (key == curveCount - 1){
							ctx.lineTo(nextCPoint.x, nextCPoint.y);
						}
					}

			      	ctx.globalAlpha = 0.35;
					ctx.stroke();
					ctx.closePath();

					ctx.restore();
				}

				function WaveContainer(){
					this.settings = {
						waveNum: 4,
					}
					this.waves = [];
					this.generateWaves();
				}

				WaveContainer.prototype.generateWaves = function(){
					var colors = ["#aaa", "#62deec", "#7272fe", "#ab05ff"];

					for (var i = 0; i < this.settings.waveNum; i++) {
						var configs = {
							curveCount: 5, // define number of curves
							maxYOffset: _.random(5, 25),
							color: colors.shift(),
							baseY: canvas.height / 2,
							speed: 0.05 + _.random(-1,2, true) / 100,
						}

						this.waves.push(new Wave(configs));
					}
				}

				WaveContainer.prototype.drawWaves = function(){
					_.forEach(this.waves, function(wave, key){
						wave.draw();
					})
				}

				function drawCanvas(){
					if (self.paused === false) {
						waveContainer.drawWaves();
						ctx.fillStyle 	= "rgba(0,0,0,0.08)";
						ctx.fillRect(0,0, canvas.width, canvas.height);
					}

					requestAnimationFrame(drawCanvas);
				}

				function init(){
					canvas.width  = innerWidth;
					canvas.height = innerHeight;

					waveContainer = new WaveContainer();

					drawCanvas(waveContainer);
				}

				init();
			},
			pauseCanvas(){
				this.paused = true;
			},
			resumeCanvas(){
				this.paused = false;
			}
		},
		data(){
			return {
				paused: false,
			}
		},
		mounted(){
			this.drawWaves();
			this.$parent.$on('pauseCanvas', this.pauseCanvas);
			this.$parent.$on('resumeCanvas', this.resumeCanvas);
		},
	}
</script>


