export const fadeOut = {
	from: {
		opacity: 1,
	},
	to: {
		opacity: 0,
	}
}

export const fadeOutLeft = {
	from: {
		opacity: 1,
		x: 0
	},
	to: {
		opacity: 0,
		x: -25
	}
}

export const fadeOutRight = {
	from: {
		opacity: 1,
		x: 0
	},
	to: {
		opacity: 0,
		x: 25
	}
}

export const fadeOutUp = {
	from: {
		opacity: 1,
		y: 0
	},
	to: {
		opacity: 0,
		y: -25
	}
}

export const fadeOutDown = {
	from: {
		opacity: 1,
		y: 0
	},
	to: {
		opacity: 0,
		y: 25
	}
}

export const fadeIn = {
	from: {
		opacity: 0,
	},
	to: {
		opacity: 1
	}
}

export const fadeInUp = {
	from: {
		opacity: 0,
		y: 25,
	},
	to: {
		opacity: 1,
		y: 0
	}
}

export const fadeInDown = {
	from: {
		opacity: 0,
		y: -25,
	},
	to: {
		opacity: 1,
		y: 0
	}
}

export const fadeInLeft = {
	from: {
		opacity: 0,
		x: 25,
	},
	to: {
		opacity: 1,
		x: 0
	}
}

export const fadeInRight = {
	from: {
		opacity: 0,
		x: -25,
	},
	to: {
		opacity: 1,
		x: 0
	}
}

export default {
	fadeOut,
	fadeOutLeft,
	fadeOutRight,
	fadeOutUp,
	fadeOutDown,
	fadeIn,
	fadeInUp,
	fadeInDown,
	fadeInLeft,
	fadeInRight
}


