export default {
	methods: {
	    animate( calledData){

            let id      = calledData.id;
            let type    = calledData.type;

            if (id !== parseInt(this.sectionId)){
                return;
            }

            if (type === 'in'){
                this.animateIn();
            }
            else if (type === 'out') {
            	this.animateOut();
            	// console.log("You're calling me to get out");
            }
        },
        emitSectionOut(){
            this.$emit('sectionOut', this.sectionId);
            // console.log(`Section ${this.sectionId} left.`);
        },
        emitSectionIn(){
            this.$emit('sectionIn', this.sectionId);
            // console.log(`Section ${this.sectionId} in.`);
        }
    },
    props: {
        sectionId: {
            type: Number,
            required: true
        }
    },
    created(){
		this.$parent.$on('animatePlz', this.animate);
		// console.log("I'm listening");
    }
}



